<template>
  <div id="app">
    <canvas id="canvas"></canvas>
  </div>
</template>

<script>
import data from './data/data.js'
import data1 from './data/data1.js'
import Charts from './main/charts.js'
import Touch from './main/touch.js'

export default {
  name: 'app',
  data: function () {
    return {
      showText: ''
    }
  },

  mounted() {
    let charts = new Charts(data1)
    let canvas = document.getElementById('canvas')
    let domApp = document.getElementById('app')
    let cw = window.getComputedStyle(domApp, false).width.replace('px', '')
    let ch = window.getComputedStyle(domApp, false).height.replace('px', '')
    let ctx = canvas.getContext('2d')
    canvas.width = cw
    canvas.height = ch
    charts.draw(canvas)
    // 阻止滚动
    document.addEventListener('touchmove', function(event){
      // 判断默认行为是否可以被禁用
      if (event.cancelable) {
        // 判断默认行为是否已经被禁用
        if (event.defaultPrevented) {
          event.preventDefault();
        }
      }
    })

  },
}

</script>

<style lang="scss">
*{
  padding: 0;
  margin: 0;
}
#app {
  width: 100%;
  height: 100vh;
}
</style>
